<template>
  <div class="big">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>菜品上下架</span>
      </div>
      <!-- 搜索区域 -->
      <div class="search-box">
        <el-form :inline="true" :model="search">
          <el-form-item>
            <el-input
              maxlength="100"
              v-model="search.keyWord"
              placeholder="请输入内容"
              style="width: 300px"
            ></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" size="small" @click="searchFood"
              >搜索</el-button
            >
          </el-form-item>
          <el-form-item>
            <el-select
              v-model="search.foodType"
              @change="foodTypeChange"
              placeholder="请选择"
            >
              <el-option value="全部"> </el-option>
              <el-option value="在售"> </el-option>
              <el-option value="待售"> </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <!-- 表格区域 -->
      <el-table :data="foodList" style="width: 100%" border stripe>
        <el-table-column label="菜品编号" prop="did" align="center">
        </el-table-column>
        <el-table-column
          label="菜品名称"
          prop="dname"
          align="center"
        ></el-table-column>
        <el-table-column label="菜品价格" prop="dprice" align="center">
        </el-table-column>
        <el-table-column label="菜品状态" align="center">
          <template v-slot="{ row }">
            <el-button
              v-if="row.status == 0"
              disabled
              type="success"
              size="mini"
            >
              在售
            </el-button>
            <el-button v-else type="info" size="mini" disabled>
              待售
            </el-button>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template v-slot="{ row }">
            <el-button
              v-if="row.status == 1"
              type="success"
              size="mini"
              icon="el-icon-top"
              title="上架"
              @click="changeStatus(row.did, 0)"
            >
            </el-button>
            <el-button
              v-else
              type="info"
              icon="el-icon-bottom"
              title="下架"
              size="mini"
              @click="changeStatus(row.did, 1)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页区域 -->
      <el-pagination
        @current-change="handleCurrentChangeFn"
        :current-page.sync="search.pagenum"
        :page-size.sync="search.pagesize"
        layout="total, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import store from "@/store";
import { getFoodListAPI, searchFoodListAPI, serveOrNotAPI } from "@/api/index";
export default {
  name: "serve_or_not",
  //   ==============================================以下是data==============================================
  data() {
    return {
      // 分页查询的参数
      search: {
        pagenum: 1,
        pagesize: 7,
        foodType: "全部",
        keyWord: "",
      },
      // 展示列表数据
      foodList: [],
      // 数据列表
      list: [],
      // 返回的数据总数
      total: 0,
    };
  },
  //   ==============================================以下是method方法==============================================
  methods: {
    // 页码值发生了变化
    handleCurrentChangeFn(newPage) {
      // 为页码值赋值
      this.search.pagenum = newPage;
      // 重新发起请求
      if (this.search.keyWord != "") return this.searchFoodFn;
      this.initFoodListFn();
    },
    // 菜品筛选发生该变
    foodTypeChange() {
      // 为页码值赋值
      this.search.pagenum = 1;
      // 重新发起请求
      if (this.search.keyWord != "") return this.searchFoodFn;
      this.initFoodListFn();
    },
    // 初始化菜品列表
    async initFoodListFn() {
      const { data: res } = await getFoodListAPI(this.search);
      if (res.code !== 0) return this.$error("获取菜品列表是失败！");
      this.total = res.count;
      this.list = res.data;
      this.foodList = this.list;
    },
    // 搜索菜品
    searchFood() {
      if (this.search.keyWord == "") return this.$message("请输入搜索内容！");
      this.search.pagenum = 1;
      this.searchFoodFn();
    },
    async searchFoodFn() {
      const { data: res } = await searchFoodListAPI(this.search);
      if (res.code != 0) return this.$message.error("搜索失败！");

      this.total = res.count;
      this.tableData = res.data;
    },
    // 菜品上下架
    async changeStatus(foodID, status) {
      let flag = status == 0 ? "上架" : "下架";
      if (flag == "上架" && store.state.shopInfo.status == 1)
        return this.$message.error("店铺停业整顿中！暂不支持菜品上架！");
      const { data: res } = await serveOrNotAPI({
        did: foodID,
        status: status,
      });

      if (res.code !== 0) return this.$message.error("菜品" + flag + "失败！");
      this.$message.success("菜品" + flag + "成功！");
      this.initFoodListFn();
    },
  },
  //   ==============================================以下是mounted方法==============================================
  mounted() {
    this.initFoodListFn();
  },
};
</script>

<style lang="less" scoped>
.big {
  margin-top: 10px;
}
// 分页器样式
.el-pagination {
  margin-top: 15px;
  text-align: center;
}
.search-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  .btn-pub {
    margin-top: 5px;
  }
}
</style>
